.radio {
    padding-left: 20px
}

.radio>label {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    padding-left: 5px
}

.radio>label::before {
    content: "";
    display: inline-block;
    position: absolute;
    width: 17px;
    height: 17px;
    left: 0;
    margin-left: -20px;
    border: 1px solid #d0cdcd;
    border-radius: 50%;
    background-color: #fff;
    -webkit-transition: border .15s ease-in-out;
    -o-transition: border .15s ease-in-out;
    transition: border .15s ease-in-out
}

.radio>label::after {
    display: inline-block;
    position: absolute;
    content: " ";
    width: 11px;
    height: 11px;
    left: 3px;
    top: 3px;
    margin-left: -20px;
    border-radius: 50%;
    -webkit-transform: scale(0, 0);
    -ms-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    transform: scale(0, 0);
    -webkit-transition: -webkit-transform .1s cubic-bezier(0.8, -.33, .2, 1.33);
    -moz-transition: -moz-transform .1s cubic-bezier(0.8, -.33, .2, 1.33);
    -o-transition: -o-transform .1s cubic-bezier(0.8, -.33, .2, 1.33);
    transition: transform .1s cubic-bezier(0.8, -.33, .2, 1.33)
}

.radio>input[type="radio"] {
    opacity: 0;
    z-index: 1
}

.radio>input[type="radio"]:focus+label::before {
    outline: 0
}

.radio>input[type="radio"]:checked+label::after {
    -webkit-transform: scale(1, 1);
    -ms-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    transform: scale(1, 1)
}

.radio>input[type="radio"]:disabled+label {
    opacity: .65;
    cursor: auto
}

.radio>input[type="radio"]:disabled+label::before {
    cursor: not-allowed
}

.radio.radio-inline {
    margin-top: 0
}

.radio-success>input[type="radio"]+label::after {
    background-color: #78b800
}

.radio-success>input[type="radio"]:checked+label::before {
    border-color: #78b800
}

.radio-success>input[type="radio"]:checked+label::after {
    background-color: #78b800
}

input[type="radio"].styled:checked+label:after {
    font-family: 'tiendanimal';
    content: "\e91a"
}

input[type="radio"] .styled:checked+label::before {
    color: #fff
}

input[type="radio"] .styled:checked+label::after {
    color: #fff
}

.radio-toolbar input[type="radio"] {
