input[type="checkbox"].checkbox-animated {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin: 0;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: 0;
    background: #fff;
    border-color: transparent
}

input[type="checkbox"].checkbox-animated:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    border-radius: 3px;
    background: transparent;
    border: 1px solid #d0cdcd;
    transition: all .3s ease-in-out
}

input[type="checkbox"].checkbox-animated:checked {
    background: transparent
}

input[type="checkbox"].checkbox-animated:checked:before {
    height: 50%;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    background: transparent;
    border-width: 3px;
    border-top-style: none;
    border-right-style: none;
    border-color: #78b800
}

input[type="checkbox"].checkbox-animated~label {
    cursor: pointer;
    font-weight: 400
}

input[type="checkbox"].checkbox-animated--error:before {
    border-color: #ff7623
}

