    line-height: normal !important;
    font-weight: 600 !important
}

.webloyaltyBanner .WLS_gtmBannerWrapper a[id^='WLS_smartButton'] {
    display: inline-block !important;
    padding: 4px 12px !important;
    margin: 7px 0 !important;
    font-size: 14px !important;
    line-height: 20px !important;
    color: #333 !important;
    text-align: center !important;
    vertical-align: middle !important;
    cursor: pointer !important;
    background-color: #f5f5f5 !important;
    background-image: -moz-linear-gradient(top, #fff, #e6e6e6) !important;
    background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#fff), to(#e6e6e6)) !important;
    background-image: -webkit-linear-gradient(top, #fff, #e6e6e6) !important;
    background-image: -o-linear-gradient(top, #fff, #e6e6e6) !important;
    background-image: linear-gradient(to bottom, #fff, #e6e6e6) !important;
    background-repeat: repeat-x !important;
    border: 1px solid #CCC !important;
    -webkit-border-radius: 4px !important;
    -moz-border-radius: 4px !important;
    border-radius: 4px !important;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#ffe6e6e6', GradientType=0) !important;
    height: auto !important;
    width: auto !important;
    min-height: auto !important;
    max-width: none !important;
    font-weight: 400 !important
}

.webloyaltyBanner .WLS_gtmBannerWrapper a>span {
    display: block
}

.webloyaltyBanner .WLS_gtmBannerWrapper div[id^='disclaimerText'] {
    font-size: 12px !important;
    line-height: normal !important
}

.webloyaltyBanner .WLS_gtmBannerWrapper br {
    display: none
}

.newsletter__title {
    font-weight: 700 !important
}

.modal_description .like-heading {
    font-size: 15px;
    font-weight: 600
}

.headline .float-left.margin-right-10 {
    margin-right: 15px
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-animation: autofill 0s forwards;
    animation: autofill 0s forwards
}

@keyframes autofill {
    100% {
        background: transparent;
        color: inherit
    }
}

@-webkit-keyframes autofill {
    100% {
        background: transparent;
        color: inherit
    }
}

.embeddedServiceLiveAgentStateChatHeader .content,
.embeddedServiceSidebarMinimizedDefaultUI .content,
.embeddedServiceLiveAgentStateChatHeader .content,
.embeddedServiceLiveAgentStateWaitingHeader .content {
    min-height: auto;
    margin-bottom: 0
}

.embeddedServiceSidebarMinimizedDefaultUI {
    border: 0
}